import React, { Component } from "react";
import Nav from "./Nav";
import Link from "next/link";
import style from "styled-components";

export class Header extends Component {
  render() {
    return (
      <div>
        <div className="bar">
          <h1>
            <Link href="">
              <a>Sick Fits</a>
            </Link>
          </h1>
        </div>
        <Nav />
        <div className="sub-bar">
          <p>Search</p>
        </div>
        <div>Cart</div>
      </div>
    );
  }
}

export default Header;
